import React from "react";
import FooterBarWrapper from "./FooterBarWrapper.js";
import resume from "./0125resume.pdf";

function Contact(props) {
	return(
		<p>
			<h4>Contact</h4>
			<a href={resume} target="_blank">Resume PDF Download</a>

			<br/>
			<FooterBarWrapper/>
		</p>
	);
}

export default Contact;


