import React,{Component} from "react";
import Portrait from "./me.jpg";
import "./AboutMe.css";

class AboutMe extends Component {
	render() {
		return(
			<p className="about-block" id="about">
				{/*<img src={Portrait} alt="Jji portrait" width="350px" style={{float: "left", marginRight: "20px"}}/>*/}
				<section>
					<h4>About</h4>
					<div className="about-intro">I'm a senior working towards a B.S. in Computer Science at
						University of California, Irvine. I'm interested in everything
						from design and UI/UX to infrastructure engineering to data science and analytics.
					</div>
					<br/>
					<div className="about-body">
						I started at UC Irvine in 2013 as a physics major. Into my third year, I realized I hated the
						research lifestyle, and decided to switch to computer science.
						<br/><br/>
						Since changing majors in 2015,
						I've attended multiple hackathons and won some prizes, started some personal projects (including
						this one), gained internship experience, and made a miniscule dent in the mountain that is
						CS knowledge.
						<br/><br/>
						In my free time, I enjoy cooking and playing table tennis and video games.

						
					</div>

					<hr/>
				</section>

			</p>
		);
	}
}

export default AboutMe;


