import React, {Component} from "react";
import "./FooterBarWrapper.css";
import GitHubPic from "./GitHub-Mark-32px.png";
import GitHubPicAlt from "./github_logo.png";
import LinkedInPic from "./In-Black-34px-TM.png";
import LinkedInPicAlt from "./In-2C-34px-TM.png";
import MailPic from "./mail.png";

class LinkedInLink extends Component {
	constructor(props) {
		super(props);
		this.state = {pic: LinkedInPic};
		this.handleMouseEnter = this.handleMouseEnter.bind(this);
		this.handleMouseLeave = this.handleMouseLeave.bind(this);
	}
	handleMouseEnter(event) {
		this.setState({pic: LinkedInPicAlt});
	}
	handleMouseLeave(event) {
		this.setState({pic: LinkedInPic});
	}
	render() {
		return(
			<a href="http://linkedin.com/in/jeffreydji" className="redirect-logo">
				<img src={this.state.pic} alt="LinkedIn-mark" onMouseEnter={this.handleMouseEnter}
					onMouseLeave={this.handleMouseLeave}/>/in/jeffreydji
			</a>
		);
	}
}

class GithubLink extends Component {
	constructor(props) {
		super(props);
		this.state = {pic: GitHubPic};
		this.handleMouseEnter = this.handleMouseEnter.bind(this);
		this.handleMouseLeave = this.handleMouseLeave.bind(this);
		this.handleMouseClick = this.handleMouseClick.bind(this);
	}

	handleMouseEnter(event) {
		this.setState({pic: GitHubPicAlt});
	}
	handleMouseLeave(event) {
		this.setState({pic: GitHubPic});
	}
	handleMouseClick(event) {
		event.preventDefault();
		window.open(this.makeHref("http://github.com/gfantom"));
	}

	render() {
		return(
			<a href="http://github.com/gfantom" className="redirect-logo">
				<img src={this.state.pic} alt="github-mark" onMouseEnter={this.handleMouseEnter}
					onMouseLeave={this.handleMouseLeave} style={{height: "32px"}}/>/gfantom
			</a>
		);
	}
}

class FooterBarWrapper extends Component {
	render() {
		return(
			<div className="footer-bar-wrapper">
				<GithubLink/>
				<LinkedInLink/>
				<a href="mailto:jdji@uci.edu" className="redirect-logo"><img src={MailPic} alt="mail-icon"
					style={{height: "32px"}}></img>jdji@uci.edu</a>
			</div>
		);
		/*
			<div className="footer-bar-wrapper">
				<GithubLink/>
			</div>
		);
		*/
	}
}

export default FooterBarWrapper;


