import React, {Component} from "react";
import {Link} from "react-router";
import "./NonHome.css";
import AboutMe from "./AboutMe.js";

class NavbarItem extends Component {
	render() {
		return(
			<Link className="navbar-item" to={this.props.link}>{this.props.tag}</Link>
		);
	}
}

class Navbar extends Component {
	render() {
		const items = [
			{
				tag:"Blog",
				cls: "blog",
				link: "/blog"
			},
			{
				tag: "About Me",
				cls: "about-me",
				link: "/about"
			},
			{
				tag: "Site",
				cls: "site",
				link: "site"
			}
		];
		return(
			<nav className="navbar">
				<Link to="/"><h3>Jeffrey Ji</h3></Link>
				{items.map((item)=>{return <NavbarItem {...item}/>;})}
			</nav>
		);
	}
}

class NonHome extends Component {
	render() {
		return(
			<div className="nonhome">
			{/*<Navbar/>*/}
			<AboutMe/>
			</div>
		);
	}
}

export default NonHome;


