import React, {Component} from "react";
import Scroll from "react-scroll";
import "./SideBar.css";

var Link = Scroll.Link;

class SideBarItem extends Component {
	constructor(props) {
		super(props);

		this.state = {
			cls: "box",
		};
		this.handleMouseOver = this.handleMouseOver.bind(this);
		this.handleMouseOut  = this.handleMouseOut.bind(this);
	}

	handleMouseOver(e) {
		this.setState({cls: "box box-active"});
	}
	handleMouseOut(e) {
		this.setState({cls: "box"});
	}

	render() {
		return(
			<Link onMouseOver={this.handleMouseOver} onMouseOut={this.handleMouseOut}
				activeClass="box box-active" className={this.state.cls} to={this.props.tag.toLowerCase()}
				spy={true} smooth={true} duration={300}>{this.props.tag}</Link>
		);
	}
}

function SideBar(props) {
	const itemList = ["About", "Work", "Contact"];

	return(
		<div className="side-bar">
			{itemList.map((i)=>{return <SideBarItem tag={i}/>;})}
		</div>
	);
}

export default SideBar;


